/* global angular */

export default function uiWmChartjsChartRangesController(
  $element,
  $scope,
  Chartjs
) {
  let rangesCanvas;
  let dotsCanvas;

  /**
   * @name createRangesChart
   * @type {function}
   * @inner
   * @description
   * Creates new ranges chart instance
   */
  const createRangesChart = ({ data, options }) => {
    if (!data) { return false; }
    rangesCanvas = $element[0].getElementsByTagName('canvas')[0];

    return new Chartjs(rangesCanvas, {
      type: 'horizontalBar',
      data,
      options,
    });
  };

  /**
   * @name createDotsChart
   * @type {function}
   * @inner
   * @description
   * Creates new dots chart instance
   */
  const createDotsChart = ({ data, options }) => {
    if (!data) { return false; }
    dotsCanvas = $element[0].getElementsByTagName('canvas')[1];

    if (options && options.tooltips && options.tooltips.custom) {
      const customTooltipFunc = options.tooltips.custom;
      Object.assign(
        options.tooltips,
        { custom: (tooltipModel) => customTooltipFunc(tooltipModel, $element[0], data.series) }
      );
    }

    return new Chartjs(dotsCanvas, {
      type: 'bubble',
      data,
      options,
    });
  };

  /**
   * @name createCharts
   * @type {function}
   * @inner
   * @description
   * Creates new chart instances
   */
  const createCharts = (dotsData, rangesData) => {
    if (!dotsData) { return false; }

    this.dotsChart = Object.assign(createDotsChart(dotsData), { $scope });
    this.rangesChart = Object.assign(createRangesChart(rangesData), { $scope });

    return {
      dotsChart: this.dotsChart,
      rangesChart: this.rangesChart,
    };
  };

  /**
   * @name updateDotsChart
   * @type {function}
   * @inner
   *
   * @description
   * Redraws chart with the new data
   *
   * @param {array} currentValue Updated series of data for chart
   * @returns {void}
   */
  const updateDotsChart = ({ data, options }) => {
    if (!data) { return; }
    if (options && options.tooltips && options.tooltips.custom) {
      const customTooltipFunc = options.tooltips.custom;
      Object.assign(
        options.tooltips,
        { custom: (tooltipModel) => customTooltipFunc(tooltipModel, $element[0], data.series) }
      );
    }

    this.dotsChart.data = data;
    this.dotsChart.options = angular.merge(this.dotsChart.options, options);
    this.dotsChart.update();
  };

  /**
   * @name updateRangesChart
   * @type {function}
   * @inner
   *
   * @description
   * Redraws chart with the new data
   *
   * @param {array} currentValue Updated series of data for chart
   * @returns {void}
   */
  const updateRangesChart = ({ data, options }) => {
    if (!data) { return; }
    this.rangesChart.data = data;
    this.rangesChart.options = angular.merge(this.rangesChart.options, options);
    this.rangesChart.update();
  };

  /**
   * @name updateCharts
   * @type {function}
   * @inner
   *
   * @param {object} dotsData Data for chart's dots
   * @param {object} rangesData Data for chart's ranges
   *
   * @description
   * Updates charts depending on received data
   *
   * @returns {void}
   */
  const updateCharts = (dotsData, rangesData) => {
    if (dotsData) {
      updateDotsChart(dotsData);
    }
    if (rangesData) {
      updateRangesChart(rangesData);
    }
  };

  /**
   * @name processCharts
   * @type {function}
   * @inner
   *
   * @description
   * Updates or creates charts depending on received data
   *
   * @returns {void}
   */
  const processCharts = () => {
    if (this.rangesChart && this.dotsChart) {
      updateCharts(this.dotsData, this.rangesData);
    } else {
      createCharts(this.dotsData, this.rangesData);
    }
  };

  /**
   * AngularJS Lifecycle hook used to draw chart
   *
   * @name $postLink
   * @type {function}
   * @returns {void}
   */
  const $postLink = () => processCharts();

  /**
   * @name $onChanges
   * @type {function}
   *
   *
   * @description
   * AngularJS Lifecycle hook used to update chart
   *
   * @param {object} state Changed params
   * @returns {void}
   */
  const $onChanges = () => processCharts();

  Object.assign(this, {
    $postLink,
    $onChanges,
  });
}



